header {
    position: fixed;
    top: 0;

    z-index: 999;
    width: 100%;
    

    background-color: rgba(25, 24, 56, 0.8);
}

.header-not-main {
    position: absolute;
    top: 40px;
}

.header-top {
    top: 40px;
}

.header {
    display: flex;
    align-items: center;

    max-width: 1334px;
    height: 60px;
    gap: 60px;

    margin-left: auto;
    margin-right: auto;

    
}

nav {
    flex-grow: 1;
}

.headerList {

    display: none;

    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }


    & li {
        border: none;

    }
}

.headerMobil {
    display: block;

    @media screen and (min-width: 767px) {
        display: none;
    }
}

.headerMobileNav {
    display: block;

    @media screen and (min-width: 767px) {
        display: none;
    }
}

.headerList a, .headerLanguage {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;

        text-transform: uppercase;

        color: #DDDDDD;
        padding: 20px 8px;
        cursor: pointer;

        transition: text-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);


    &:hover, &:focus {
        border: none;
        outline: none;
        text-shadow: 0px 0px 10px #16DCF2;
    }

}

.headerLanguage {
    display: none;

    @media screen and (min-width: 767px) {
        display: block;
        background-color: transparent;
        border: none;
    }


    & option {
        color: #DDDDDD;

        border: none;
        border-radius: 3px;
        background-color: #011126;
    }
}
